<template>
  <v-container>
    <v-row class="ma-0 pa-0">
      <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <vue-country-code style="display:none" />
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="margin-right:10px;cursor: pointer;" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/sync_toolbar.png" @click="syncOrders" width="50px" />
          </template>
          <span>Synchronization</span>
        </v-tooltip>
      </v-col>
      <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-menu v-model="fromDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field v-model="fromDate" label="From" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
          </template>
          <v-date-picker v-model="fromDate" @input="fromDateMenu = false"></v-date-picker>
        </v-menu>        
      </v-col>
      <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-menu v-model="toDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field v-model="toDate" label="To" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
          </template>
          <v-date-picker v-model="toDate" @input="toDateMenu = false"></v-date-picker>
        </v-menu>
      </v-col>
      <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-text-field hide-details label="Search" placeholder="Search" dense outlined v-model="searchText" @keyup="keyEnter" ref="search" style="width: 270px;top:10px"></v-text-field>
      </v-col>
      <v-col align="left" class="ma-0 pa-0 flex-grow-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:10px" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/magnify.png" @click="setSearch" width="40px" />
          </template>
          <span>Search</span>
        </v-tooltip>
      </v-col>
      <v-col class="ma-1 pa-1 flex-grow-0 flex-shrink-0"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; </v-col>
      <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/write_toolbar.png" @click="setNewOrder" width="40px" />
          </template>
          <span>New Order</span>
        </v-tooltip>
      </v-col>
      <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:2px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/upload-file_toolbar.png" @click="setExcelUpload" width="43px" />
          </template>
          <span>Upload Excel</span>
        </v-tooltip>
      </v-col>
      <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:2px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/download-file_toolbar.png" @click="getMakeExcel()" width="43px" />
          </template>
          <span>Download Excel</span>
        </v-tooltip>
      </v-col>
      <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:4px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/merge_toolbar.png" width="45px" />
          </template>
          <span>Merge</span>
        </v-tooltip>
      </v-col>
      <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:4px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/split_toolbar.png" width="45px" />
          </template>
          <span>Split</span>
        </v-tooltip>
      </v-col>
      <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:4px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/rubbish-bin_toolbar.png" @click="setDelete" width="45px" />
          </template>
          <span>Delete</span>
        </v-tooltip>
      </v-col>
      <v-col align="right" class="ma-1 pa-0 flex-shrink-0">
        <v-tooltip top color="#D18EA8">
          <template v-slot:activator="{ on, attrs }">
            <v-img v-on="on" v-bind="attrs" style="cursor:pointer" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/shipment_toolbar.png" @click="setShipping" width="150px"/> 
          </template>
          <span>Shipping</span>
        </v-tooltip>
      </v-col>      
    </v-row>
    <v-row>
      <v-col align="left" style="padding-top:0px;padding-bottom:0px;">
        Total : {{ desserts.length }} / Selected : {{ selected.length }}
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-data-table :headers="headers" :items="desserts" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent">
          <template v-slot:item="props">
            <tr>
              <td style="height:30px;" align="center">
                <input type="checkbox" style="width:20px;height:20px" @change="setCheck(props.item, $event.target.checked)" name="item">
              </td>
              <td  align="start" style="height:30px;; cursor:pointer" @click="getOrderDetail(props.item)">
                {{ props.item.gemMasterNo }}
              </td>
              <td style="height:30px;" align="start">
                <v-row>
                  <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                    <img alt="" class="shrink mt-1 hidden-sm-and-down" contain :src="getPlatformImgUrl(props.item.storePlatform)" width="20px"/>
                  </v-col>
                  <v-col align="left" class="ma-1 pa-1  flex-shrink-0">
                    <div style="width:90px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis">{{ props.item.orderNo }}</div>
                  </v-col>
                </v-row>
              </td>
              <td style="height:30px;" align="start">{{ props.item.orderedAt }}</td>
              <td style="height:30px;" align="start">
                <div style="width:150px;overflow:hidden;white-space:nowrap;text-overflow:clip">{{ props.item.recipientName }}</div><div style="margin:1px" :class="getCountry(props.item.recipientAddressCountryCode)"></div>&nbsp;({{props.item.recipientAddressCountryCode}})
              </td>




              <td v-if="props.item.orderItems.length != 0" style="height:30px;" align="start">
                <div style="width:150px;overflow:hidden;white-space:nowrap;text-overflow:clip">{{ props.item.orderItems[0].itemQuantity }} X {{ props.item.orderItems[0].itemName }}<br />
                Qty: {{ props.item.orderItems | totalQuantity }}, Value: {{ props.item.orderItems[0].itemValueCurrency | currency }} {{ props.item.itemTotalValue | comma }} </div>
              </td>
              <td v-else style="height:30px;" align="start">
                Qty: {{ props.item.orderItems | totalQuantity }}, Value: {{ props.item.itemTotalValue | comma }} 
              </td>
              <td style="height:30px;" align="start">
                {{ props.item.shipmentActualWeight }} <span v-if="props.item.weightUnit == null || props.item.weightUnit == ''">kg</span><span v-else>{{ props.item.weightUnit }}</span> 
                / {{ props.item.shipmentVolumeWeight }} <span v-if="props.item.weightUnit == null || props.item.weightUnit == ''">kg</span><span v-else>{{ props.item.weightUnit }}</span> 
              </td>
              <td style="height:30px;" align="start"></td>
              <td style="height:30px;" align="start">{{ props.item.shippingTerm }}</td>
              <td style="height:30px;" align="start">{{ props.item.serviceType }}</td>
              <td style="height:30px;" align="left">
                <v-row>
                  <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                    <img alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/merge.png" width="25px"/>
                  </v-col>
                  <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                    <img alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/split3.png" width="25px"/>
                  </v-col>
                </v-row>
              </td>

              




            </tr>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
    <v-row class="ma-0 pa-0">
      <v-col class="ma-0 pa-0">
        <v-dialog v-model="excelUploadDialog" persistent max-width="1000px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">Order Excel Upload</span>
            </v-card-title>
            <v-card-text class="ma-0 pa-0">
              <v-container>
                <v-row>
                  <v-col align="left"> Total Count : {{ excelRowList.length }}&nbsp; </v-col>
                  <v-col align="right">
                    <v-btn color="success" href="/GEM_Order_Upload_Template.xlsx" download> Template Download </v-btn>&nbsp;
                    <input type="file" @change="readFile" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" id="excelFileInput" ref="excelFileInput" style="display: none"/>
                    <v-btn color="success" @click="$refs.excelFileInput.click()"> File Search </v-btn>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <v-data-table
                      :headers="excelHeaders"
                      :items="excelRowList"
                      item-key="orderno"
                      class="elevation-1"
                      disable-pagination
                      hide-default-footer
                      multi-sort
                      fixed-header
                      height="300"
                    >
                    </v-data-table>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="error" outlined @click="setExcelUploadCancel"> Cancel </v-btn>
              <v-btn color="primary" @click="setExcelUploadOk"> Ok </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- 주문 수정 다이얼로그 -->
        <v-dialog v-model="orderDetailModifyDialog" persistent max-width="1300px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
          </template>
          <v-card>
            <v-card-text class="ma-0 pa-0">
              <v-container>
                <v-row class="ma-0 pa-0">
                  <v-col align="left" class="ma-0 pa-0">
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-0 pa-0">
                        <table style="margin-bottom: 10px">
                          <tr>
                            <td>
                              <v-img
                                alt=""
                                class="shrink mt-1 hidden-sm-and-down"
                                contain
                                src="@/images/info.png"
                                width="40px"
                              />
                            </td>
                            <td>
                              <b>{{ orderDetail.gemMasterNo }}</b>
                            </td>
                          </tr>
                        </table>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Marketplace/Platform (*)"
                          placeholder="Marketplace/Platform (*)"
                          outlined
                          v-model="orderDetail.storePlatform"
                          ref="storePlatform"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Store Name (*)"
                          placeholder="Store Name (*)"
                          outlined
                          v-model="orderDetail.storeName"
                          ref="storeName"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Order Number (*)"
                          placeholder="Order Number (*)"
                          outlined
                          v-model="orderDetail.orderNo"
                          ref="orderNo"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-menu v-model="orderDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field v-model="orderDetail.orderedAt" label="Order Date (*)" dense outlined readonly v-bind="attrs" v-on="on" hide-details style="width: 200px" :disabled="actionMethod == 'Modify'"></v-text-field>
                          </template>
                          <v-date-picker v-model="orderDetail.orderedAt" @input="orderDateMenu=false" :disabled="actionMethod == 'Modify'"></v-date-picker>
                        </v-menu>       
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-select
                          hide-details
                          :items="shippingTermItems"
                          label="Shipping Term (*)"
                          outlined
                          dense
                          style="width: 100px"
                          v-model="orderDetail.shippingTerm"
                          ref="shippingTerm"
                          placeholder="Shipping Term (*)"
                        ></v-select>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-select
                          hide-details
                          :items="packageTypeItems"
                          item-text="name"
                          item-value="value"
                          style="width:100px"
                          label="Package Type"
                          placeholder="Package Type"
                          v-model="orderDetail.packageType"
                          ref="packageType"
                          outlined
                          dense
                        ></v-select>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-select
                          hide-details
                          :items="contentsTypeItems"
                          item-text="name"
                          item-value="value"                          
                          style="width:100px"
                          label="Contents Type (*)"
                          placeholder="Contents Type (*)"
                          v-model="orderDetail.itemContentsType"
                          ref="itemContentsType"
                          dense
                          outlined
                        ></v-select>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-select
                          hide-details
                          :items="serviceTypeItems"
                          item-text="name"
                          item-value="value"
                          label="Service Type"
                          outlined
                          dense
                          style="width: 100px"
                          v-model="orderDetail.serviceType"
                          ref="serviceType"
                          placeholder="Service Type"
                        ></v-select>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col align="left" class="ma-0 pa-0">
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-0 pa-0">
                        <table style="margin-bottom: 10px">
                          <tr>
                            <td>
                              <v-img
                                alt=""
                                class="shrink mt-1 hidden-sm-and-down"
                                contain
                                src="@/images/sender.png"
                                width="40px"
                              />
                            </td>
                            <td><b>Sender</b></td>
                          </tr>
                        </table>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Name (*)"
                          placeholder="Name (*)"
                          outlined
                          v-model="orderDetail.senderName"
                          ref="senderName"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Company"
                          placeholder="Company"
                          outlined
                          v-model="orderDetail.senderCompany"
                          ref="senderCompany"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Email (*)"
                          placeholder="Email (*)"
                          outlined
                          v-model="orderDetail.senderEmail"
                          ref="senderEmail"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Phone Number (*)"
                          placeholder="Phone Number (*)"
                          outlined
                          v-model="orderDetail.senderPhoneNo"
                          oninput="javascript: this.value = this.value.replace(/\D/g, '' );"
                          ref="senderPhoneNo"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Mobile Number (*)"
                          placeholder="Mobile Number (*)"
                          outlined
                          v-model="orderDetail.senderMobileNo"
                          oninput="javascript: this.value = this.value.replace(/\D/g, '' );"
                          ref="senderMobileNo"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <vue-country-code
                          @onSelect="onCountrySelectSender"
                          v-bind:defaultCountry="orderDetail.senderAddressCountryCode"
                          style="margin-right: 5px"
                        >
                        </vue-country-code>
                        <v-text-field
                          hide-details
                          label="Country Code"
                          placeholder="Country Code"
                          outlined
                          v-model="orderDetail.senderAddressCountryCode"
                          ref="senderAddressCountryCode"
                          dense
                          filled
                          disabled
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Street1 (*)"
                          placeholder="Street1 (*)"
                          outlined
                          v-model="orderDetail.senderAddress1"
                          ref="senderAddress1"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Street2"
                          placeholder="Street2"
                          outlined
                          v-model="orderDetail.senderAddress2"
                          ref="senderAddress2"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="City (*)"
                          placeholder="City (*)"
                          outlined
                          v-model="orderDetail.senderAddressCity"
                          ref="senderAddressCity"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0" id="senderAddrssStateSelect" style="display:none">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-select
                          hide-details
                          :items="senderAddressStateItems"
                          item-text="name"
                          item-value="value"
                          label="State/Province (*)"
                          outlined
                          dense
                          style="width: 100px"
                          v-model="orderDetail.senderAddressStateCode"
                          ref="senderAddressStateCode"
                          placeholder="senderAddressStateCode"
                          @change="onSenderAddressStateCodeChange()"
                        ></v-select>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0" id="senderAddrssStateText" style="display:none">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="State/Province"
                          placeholder="State/Province"
                          outlined
                          v-model="orderDetail.senderAddressState"
                          ref="senderAddressState"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Post Code (*)"
                          placeholder="Post Code (*)"
                          outlined
                          v-model="orderDetail.senderZipCode"
                          ref="senderZipCode"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col align="left" class="ma-0 pa-0">
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-0 pa-0">
                        <table style="margin-bottom: 10px">
                          <tr>
                            <td>
                              <v-img
                                alt=""
                                class="shrink mt-1 hidden-sm-and-down"
                                contain
                                src="@/images/recipient.png"
                                width="40px"
                              />
                            </td>
                            <td><b>Recipient</b></td>
                          </tr>
                        </table>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Name (*)"
                          placeholder="Name (*)"
                          outlined
                          v-model="orderDetail.recipientName"
                          ref="recipientName"
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row class="ma-0 pa-0">
                      <v-col class="ma-1 pa-0" style="display: flex">
                        <v-text-field
                          hide-details
                          label="Local Name"
                          placeholder="Local Name"
                          outlined
                          v-model="orderDetail.recipientLocalName"
