        if(this.orderDetail.recipientAddressStateCode == null || this.orderDetail.recipientAddressStateCode == '') { this.$alert(common.messages.recipientAddressState, "", "info").then(() => { this.$refs.recipientAddressStateCode.focus(); }); return false; }
        if(this.orderDetail.recipientAddressState == null || this.orderDetail.recipientAddressState == '') { this.$alert(common.messages.recipientAddressState, "", "info").then(() => { this.$refs.recipientAddressState.focus(); }); return false; }
        if(!commFunction.stateCodeCheck(this.orderDetail.recipientAddressCountryCode.toUpperCase(), this.orderDetail.recipientAddressStateCode.toUpperCase())) {
          this.$alert(common.messages.recipientAddressState, '', 'info').then(() => { this.$refs.recipientAddressStateCode.focus(); }); return false;
        }
      }
      
      if((this.orderDetail.recipientAddressCity + " " + this.orderDetail.recipientAddressState).length > 46) { this.$alert(common.messages.recipientCity45, '', 'info').then(() => { this.$refs.recipientAddressCity.focus(); }); return false; }

      if(this.orderDetail.recipientZipCode == null || this.orderDetail.recipientZipCode == '') { this.$alert(common.messages.recipientZipCode, "", "info").then(() => { this.$refs.recipientZipCode.focus(); }); return false; }
      if(this.orderDetail.recipientZipCode.length > 20) { this.$alert(common.messages.recipientZipCode20, '', 'info').then(() => { this.$refs.recipientZipCode.focus(); }); return false; }

      if(this.orderDetail.recipientAddressCountryCode == null || this.orderDetail.recipientAddressCountryCode == '') { this.$alert(common.messages.recipientAddressCountryCode, "", "info").then(() => { this.$refs.recipientAddressCountryCode.focus(); }); return false; }

      if(this.orderDetail.shipmentActualWeight == null || this.orderDetail.shipmentActualWeight == '') { this.$alert(common.messages.shipmentActualWeight, "", "info").then(() => { this.$refs.shipmentActualWeight.focus(); }); return false; }
      if(!checkFloat.test(String(this.orderDetail.shipmentActualWeight))) { this.$alert(common.messages.shipmentActualWeightFloat, '', 'info').then(() => { this.$refs.shipmentActualWeight.focus(); }); return false; }

      if(this.orderDetail.shipmentVolumeWeight == null || this.orderDetail.shipmentVolumeWeight == '') { this.$alert(common.messages.shipmentVolumeWeight, "", "info").then(() => { this.$refs.shipmentVolumeWeight.focus(); }); return false; }
      if(!checkFloat.test(String(this.orderDetail.shipmentVolumeWeight))) { this.$alert(common.messages.shipmentVolumeWeightFloat, '', 'info').then(() => { this.$refs.shipmentVolumeWeight.focus(); }); return false; }

      if(this.orderDetail.weightUnit != null && this.orderDetail.weightUnit != "" && this.orderDetail.weightUnit.length > 10) { this.$alert(common.messages.weightUnit, '', 'info').then(() => { this.$refs.weightUnit.focus(); }); return false; }

      if(this.orderDetail.dimensionWidth != "" && !checkFloat.test(String(this.orderDetail.dimensionWidth))) { this.$alert(common.messages.width, '', 'info').then(() => { this.$refs.dimensionWidth.focus(); }); return false; }
      if(this.orderDetail.dimensionLength != "" && !checkFloat.test(String(this.orderDetail.dimensionLength))) { this.$alert(common.messages.length, '', 'info').then(() => { this.$refs.dimensionLength.focus(); }); return false; }
      if(this.orderDetail.dimensionHeight != "" && !checkFloat.test(String(this.orderDetail.dimensionHeight))) { this.$alert(common.messages.height, '', 'info').then(() => { this.$refs.dimensionHeight.focus(); }); return false; }

      if(this.orderDetail.parcelCount == null || this.orderDetail.parcelCount == '' || this.orderDetail.parcelCount == 0) { this.$alert(common.messages.parcelCount, "", "info").then(() => { this.$refs.parcelCount.focus(); }); return false; }
  
      if (this.orderDetail.orderItems.length <= 0) { this.$alert(common.messages.orderItemsLength, "", "info").then(() => { }); return false; }

      if((this.orderDetail.recipientAddressCountryCode == "RU" || this.orderDetail.recipientAddressCountryCode == "GE" || this.orderDetail.recipientAddressCountryCode == "AM") 
        && (this.orderDetail.serviceType == null || this.orderDetail.serviceType == "")) { this.$alert(common.messages.serviceType, '', 'info').then(() => { this.$refs.serviceType.focus(); }); return false; }
      if(this.orderDetail.recipientAddressCountryCode == "RU" || this.orderDetail.recipientAddressCountryCode == "GE" || this.orderDetail.recipientAddressCountryCode == "AM") {
        if(this.orderDetail.serviceType.toUpperCase() == 'D2D' || this.orderDetail.serviceType.toUpperCase() == 'POD') { true; }
        else { this.$alert(common.messages.serviceTypeFilter, '', 'info').then(() => { this.$refs.serviceType.focus(); }); return false; }
      }

      for(var itemsCnt=0; itemsCnt<this.orderDetail.orderItems.length; itemsCnt++)
      {
        if(this.orderDetail.orderItems[itemsCnt].itemName == null || this.orderDetail.orderItems[itemsCnt].itemName == "") { this.$alert(common.messages.itemName, "", "info").then(() => { }); return false; }
        
        if(this.orderDetail.orderItems[itemsCnt].itemName.length > 100) { this.$alert(common.messages.itemNameLength + "(" + this.orderDetail.orderItems[itemsCnt].itemName + ")", "", "info").then(() => { }); return false; }

        if(this.orderDetail.orderItems[itemsCnt].itemQuantity == null || this.orderDetail.orderItems[itemsCnt].itemQuantity == "") { this.$alert(common.messages.itemQuantity, "", "info").then(() => { }); return false; }
        if(!checkNumber.test(this.orderDetail.orderItems[itemsCnt].itemQuantity)) { this.$alert(common.messages.itemQuantityNumber, '', 'info').then(() => { }); return false; }

        if(this.orderDetail.orderItems[itemsCnt].itemUnitValue == null || this.orderDetail.orderItems[itemsCnt].itemUnitValue == "") { this.$alert(common.messages.itemUnitValue, "", "info").then(() => { }); return false; }
        if(!checkFloat.test(this.orderDetail.orderItems[itemsCnt].itemUnitValue)) { this.$alert(common.messages.itemUnitValueFloat, '', 'info').then(() => { }); return false; }

        if (this.orderDetail.orderItems[itemsCnt].itemValueCurrency == null || this.orderDetail.orderItems[itemsCnt].itemValueCurrency == "") { this.$alert(common.messages.itemValueCurrency, "", "info").then(() => { }); return false; }
        if (this.orderDetail.recipientAddressCountryCode == "JP" && this.orderDetail.orderItems[itemsCnt].itemValueCurrency != "JPY") {
            { this.$alert(common.messages.itemValueCurrencyJPY, "", "info").then(() => {}); return false; }
        }
        var defaultCurrenty = this.orderDetail.orderItems[0].itemValueCurrency

        if (defaultCurrenty != this.orderDetail.orderItems[itemsCnt].itemValueCurrency) { this.$alert(common.messages.itemValueCurrency, "", "info").then(() => { }); return false; }
        
        if(this.orderDetail.orderItems[itemsCnt].itemWeight == null || this.orderDetail.orderItems[itemsCnt].itemWeight == "") { this.$alert(common.messages.itemWeight, "", "info").then(() => { }); return false; }
        if(!checkFloat.test(this.orderDetail.orderItems[itemsCnt].itemWeight)) { this.$alert(common.messages.itemWeightFloat, '', 'info').then(() => { }); return false; }

        if(this.orderDetail.orderItems[itemsCnt].itemHSCode == null || this.orderDetail.orderItems[itemsCnt].itemHSCode == "") { this.$alert(common.messages.itemHSCode, "", "info").then(() => { }); return false; }
        if(!checkFloat.test(this.orderDetail.orderItems[itemsCnt].itemHSCode)) { this.$alert(common.messages.itemHSCodeFloat, '', 'info').then(() => { }); return false; }

        if(this.orderDetail.orderItems[itemsCnt].itemOriginCountryCode == null || this.orderDetail.orderItems[itemsCnt].itemOriginCountryCode == "") { this.$alert(common.messages.itemOriginCountryCode, "", "info").then(() => { }); return false; }

        if((this.orderDetail.recipientAddressCountryCode == "RU" || this.orderDetail.recipientAddressCountryCode == "AM" || this.orderDetail.recipientAddressCountryCode == "GE" || this.orderDetail.recipientAddressCountryCode == "JP")
         && (this.orderDetail.orderItems[itemsCnt].itemUrl == null || this.orderDetail.orderItems[itemsCnt].itemUrl == "")) { this.$alert(common.messages.itemSalesURL, '', 'info').then(() => { }); return false; }
  
        if(this.orderDetail.orderItems[itemsCnt].itemBarcode != null && this.orderDetail.orderItems[itemsCnt].itemBarcode != "" && this.orderDetail.orderItems[itemsCnt].itemBarcode.length > 30) { this.$alert(common.messages.itemBarcode30, '', 'info').then(() => { }); return false; }
  
        if(this.orderDetail.orderItems[itemsCnt].itemSKU != null && this.orderDetail.orderItems[itemsCnt].itemSKU != "" && this.orderDetail.orderItems[itemsCnt].itemSKU.length > 255) { this.$alert(common.messages.itemSKU255, '', 'info').then(() => { }); return false; }
  
        if (this.orderDetail.orderItems[itemsCnt].parcelNo == null || this.orderDetail.orderItems[itemsCnt].parcelNo == "") { this.$alert(common.messages.itemParcelIndex, "", "info").then(() => { }); return false; }
        if (this.orderDetail.parcelCount < this.orderDetail.orderItems[itemsCnt].parcelNo) { this.$alert(common.messages.parcelCountIndex, "", "info").then(() => { }); return false; }
      }

      var loginId = this.$session.get("loginId");
      var authorization = this.$session.get("authorization");

      if(this.actionMethod == "Modify")
      {
        axios
        .put(common.makeAPIUrl("/sellers/" + loginId + "/orders/" + this.orderDetail.id), this.orderDetail, {
          headers: { "Content-Type": "application/json", Authorization: authorization },
        })
        .then((response) => {
          if (response.status == 200) {
            this.$alert(common.messages.orderModifyComplate, "", "info").then(() => {});
            this.orderDetailModifyDialog = false;
            this.getDataInit();
          }
        })
        .catch((err) => {
          this.$alert(err.response.data.message, "", "info").then(() => {});
        });
      } else if(this.actionMethod == "Add") {
        axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/orders'), this.orderDetail, { headers: {'Authorization': this.$session.get('authorization')}})
        .then(response => {                
          if(response.status == 200) {
            this.$alert(common.messages.orderNewComplate, '', 'info').then(() => {  })
            this.orderDetailModifyDialog = false
            this.getDataInit()
          }
        })
        .catch(err => {
          this.$alert(err.response.data.message, '', 'info').then(() => {  })
        })
      } else {
        this.$alert("System Method Error.", "", "info").then(() => {});
      }
    },
    setOrderDetailModifyCancel() {
      this.orderDetailModifyDialog = false;
    },
    setOrderModify() {
      if (this.orderDetail.isDeleted == "Y") {
        this.$alert(common.messages.orderIsDeleted, "", "info").then(() => {});
        return false;
      }
      this.orderDetailDialog = false;
      this.orderDetailModifyDialog = true;
      this.actionMethod = 'Modify'
    },
    getOrderDetail(order) {
      this.setOrderDetail.orderDetail = JSON.parse(JSON.stringify(order));
      this.orderDetail = JSON.parse(JSON.stringify(order));
      this.setOrderDetail.show = true
      this.setOrderDetail.deleteBtnShow = true
      this.setOrderDetail.modifyBtnShow = true
    },
    setSearch() {
      this.getDataInit();
    },
    syncOrders() {
        this.setLoadingBar.loadingBarDialogShow = true;
        axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/orders-collection')
                    , null
                    , { headers: {'Authorization': this.$session.get('authorization')}}
        ).then(() => {
            this.setLoadingBar.loadingBarDialogShow = false;
            this.getDataInit();
        })
        .catch(err => {
            if(err.response.status == 401) {
                this.$alert(common.messages.login, "", "info").then(() => {
                    this.$router.push("/");
                });

                return false;
            }        
            this.$alert(err.response.data.message, '', 'info').then(() => {  })
            this.setLoadingBar.loadingBarDialogShow = false;
        })
    },
    setShipping() {
      this.selectedTasks = this.selected;
      var orderList = [];
      if (this.selectedTasks.length == 0) {
        this.$alert(common.messages.orderSelect, "", "info").then(() => {});
        return false;
      } else {
        this.$confirm(common.messages.orderShipment, "", "question").then(() => {
          this.setLoadingBar.loadingBarDialogShow = true;
          for (var orderCount = 0; orderCount < this.selectedTasks.length; orderCount++) {
            var order = new Object();
            order.id = 0;
            order.orderId = this.selectedTasks[orderCount].id;
            order.sellerId = this.$session.get("loginId");
            
            orderList.push(order);
          }

          axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/shipments'), orderList, { headers: {'Authorization': this.$session.get('authorization')}})
          .then(response => {                
            if(response.status == 200) {
              this.$alert(common.messages.orderShipmentComplate, '', 'info').then(() => {  })
              this.getDataInit()
            }
          })
          .catch(err => {
            this.setLoadingBar.loadingBarDialogShow = false;
            this.$alert(err.response.data.message, '', 'info').then(() => {  })
          })
        });
      }
    },
    setBarcodeCancel() {
      $(document).prop("title", "GEM Platform");
      this.barcodeDialog = false;
    },
    setExcelUpload() {
      this.excelRowList = [];
      $("#excelFileInput").val("");
      this.excelUploadDialog = true;
    },
    setExcelUploadCancel() {
      this.excelUploadDialog = false;
    },
    getDataInit() {
      document.querySelector('.v-data-table__wrapper').scrollTo({ left: 0, top: 0 });
      this.setLoadingBar.loadingBarDialogShow = true;
      axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/orders?startDate=' + this.fromDate + '&endDate=' + this.toDate + '&keyword='+ this.searchText + '' + '&isDeleted=false')
      , { headers: {'Authorization': this.$session.get('authorization')}})
      .then(response => {
        for(var orderCount = 0; orderCount < response.data.length; orderCount++)
        {
          response.data[orderCount].orderedAt = common.getYYYMMDD(response.data[orderCount].orderedAt)
        }
        this.main = response.data;
        this.desserts = [];
        this.selectedTasks = [];
        this.selected = [];
        this.currencyCount = 0;
        this.setLoadingBar.loadingBarDialogShow = false;
        $('input[name="allCheck"]').prop('checked', false);
        this.getDataList();
      })
      this.setLoadingBar.loadingBarDialogShow = false;
    },    
    getAllView() {
      if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
        this.$alert(common.messages.login, "", "info").then(() => {
          this.$router.push("/");
        });
        return false;
      }
      this.setLoadingBar.loadingBarDialogShow = true;
      var vm = this;

      setTimeout(function() {
        for(vm.currencyCount; vm.currencyCount<vm.main.length; vm.currencyCount++) {
          vm.desserts.push(vm.main[vm.currencyCount]); 
        }
        this.setLoadingBar.loadingBarDialogShow = false;
      }, 500);
    },
    getDataList() {
      if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
        this.$alert(common.messages.login, "", "info").then(() => {
          this.$router.push("/");
        });
        return false;
      }
      this.setLoadingBar.loadingBarDialogShow = true;
      var vm = this;

      setTimeout(function() {
        //var checkCount = 0;
        for(vm.currencyCount; vm.currencyCount<vm.main.length; vm.currencyCount++) {
          /*
          checkCount++;
          if(checkCount <= 100) {
            vm.desserts.push(vm.main[vm.currencyCount]); 
          } else {
            break;
          }
          */
          vm.desserts.push(vm.main[vm.currencyCount]); 
        }
        vm.setLoadingBar.loadingBarDialogShow = false;
      }, 500);
    },
    setExcelUploadOk() {
      var orders = [];

      for(var excelRowCount=0; excelRowCount<this.excelRowList.length; excelRowCount++) {
        var orderCheck = false
        var orderCheckCnt = 0
        var order = new Object();
        var item = new Object();
        order.orderItems = [];

        //주문번호는 같으나 받는사람 or 플랫폼이 다르면 Alert 발생
        for(var excelCnt=0; excelCnt<orders.length; excelCnt++) {
          if(this.excelRowList[excelRowCount].orderNo == orders[excelCnt].orderNo) {
            if(this.excelRowList[excelRowCount].recipientName.trim().toUpperCase() != orders[excelCnt].recipientName.trim().toUpperCase()
            || this.excelRowList[excelRowCount].storePlatform.trim().toUpperCase() != orders[excelCnt].storePlatform.trim().toUpperCase())
            {
              this.$alert(common.messages.orderNo + ' (' + this.excelRowList[excelRowCount].orderNo + ')', '', 'info').then(() => { this.excelUploadDialog = false }); return false;
            }
            else
            {
              orderCheck = true
              orderCheckCnt = excelCnt
            }
          }
        }

        this.excelRowList[excelRowCount].orderedAt = String(this.excelRowList[excelRowCount].orderedAt).substring(0, 4) + '-' + String(this.excelRowList[excelRowCount].orderedAt).substring(4, 6) + '-' + String(this.excelRowList[excelRowCount].orderedAt).substring(6, 8)
        this.excelRowList[excelRowCount].senderAddressCountryCode = String(this.excelRowList[excelRowCount].senderAddressCountryCode).toUpperCase()
        this.excelRowList[excelRowCount].recipientAddressCountryCode = String(this.excelRowList[excelRowCount].recipientAddressCountryCode).toUpperCase()
        this.excelRowList[excelRowCount].itemOriginCountryCode = String(this.excelRowList[excelRowCount].itemOriginCountryCode).toUpperCase()
        this.excelRowList[excelRowCount].itemValueCurrency = String(this.excelRowList[excelRowCount].itemValueCurrency).toUpperCase()
        if(this.excelRowList[excelRowCount].serviceType != null) { this.excelRowList[excelRowCount].serviceType = String(this.excelRowList[excelRowCount].serviceType).toUpperCase() }
        if(this.excelRowList[excelRowCount].package != null) { this.excelRowList[excelRowCount].package = String(this.excelRowList[excelRowCount].package).toUpperCase() }
        this.excelRowList[excelRowCount].itemContentsType = String(this.excelRowList[excelRowCount].itemContentsType).toUpperCase()

        if(this.excelRowList[excelRowCount].recipientAddressCountryCode =='JP'){
          const japaneseRegex = /^[^a-zA-Z가-힣]*$/;
                if (!japaneseRegex.test(this.excelRowList[excelRowCount].recipientLocalName)) {
                    this.$alert(common.messages.recipientLocalNameJapan, "", "info").then(() => {}); return false;  
                } 
                if (!japaneseRegex.test(this.excelRowList[excelRowCount].recipientAddress1)) {
                    this.$alert(common.messages.recipientAddressJapanAddress1, "", "info").then(() => {}); return false;  
                } 
                if(this.excelRowList[excelRowCount].recipientAddress2.length > 0){
                  if (!japaneseRegex.test(this.excelRowList[excelRowCount].recipientAddress2)) {
                    this.$alert(common.messages.recipientAddressJapanAddress2, "", "info").then(() => {}); return false;  
                  }
                }
                if (!japaneseRegex.test(this.excelRowList[excelRowCount].recipientAddressCity)) {
                    this.$alert(common.messages.recipientAddressJapanAddressCity, "", "info").then(() => {}); return false;  
                }
        }

        const EngNumRegex = /^(?=.*[a-zA-Z])(?=.*\d).+$/;

        if(orderCheck) { //이미 주문건이 있으면 Item ADD
          item.itemName = this.excelRowList[excelRowCount].itemName
          item.itemQuantity  = this.excelRowList[excelRowCount].itemQuantity 
          item.itemUnitValue  = this.excelRowList[excelRowCount].itemUnitValue 
          
          if (this.excelRowList[excelRowCount].recipientAddressCountryCode == "JP" && this.excelRowList[excelRowCount].itemValueCurrency != "JPY") {
            { this.$alert(common.messages.itemValueCurrencyJPY, "", "info").then(() => {}); return false; }
          } else {
            item.itemValueCurrency  = this.excelRowList[excelRowCount].itemValueCurrency 
          }
          
          item.itemWeight  = this.excelRowList[excelRowCount].itemWeight 
          item.itemHSCode  = this.excelRowList[excelRowCount].itemHSCode 
          item.itemOriginCountryCode  = this.excelRowList[excelRowCount].itemOriginCountryCode 
          if (this.excelRowList[excelRowCount].recipientAddressCountryCode == "JP") {
            if (this.excelRowList[excelRowCount].itemUrl == null || this.excelRowList[excelRowCount].itemUrl == ""){
              { this.$alert(common.messages.itemSalesURL, "", "info").then(() => {}); return false; }
            } else if (this.excelRowList[excelRowCount].itemUrl.length > 200) {
              { this.$alert(common.messages.itemSalesURL200, "", "info").then(() => {}); return false; }
            } else {
            item.itemUrl  = this.excelRowList[excelRowCount].itemUrl 
            }
          } else{
            item.itemUrl  = this.excelRowList[excelRowCount].itemUrl 
          }
          if (this.excelRowList[excelRowCount].recipientAddressCountryCode == "JP"){
            if (this.excelRowList[excelRowCount].itemBarcode == null || this.excelRowList[excelRowCount].itemBarcode == ""){
              { this.$alert(common.messages.itemBarcode, "", "info").then(() => {}); return false; }
            } else if (this.excelRowList[excelRowCount].itemBarcode.length > 20){
              { this.$alert(common.messages.itemBarcode20, "", "info").then(() => {}); return false; }
            } else if (!EngNumRegex.test(this.excelRowList[excelRowCount].itemBarcode)){
              { this.$alert(common.messages.itemBarcodeMixEngNum, "", "info").then(() => {}); return false; }
