          order.recipientTaxId = this.excelRowList[excelRowCount].recipientTaxId
          order.recipientAddress1 = this.excelRowList[excelRowCount].recipientAddress1
          order.recipientAddress2 = this.excelRowList[excelRowCount].recipientAddress2
          order.recipientAddressCity = this.excelRowList[excelRowCount].recipientAddressCity
          order.recipientAddressState = this.excelRowList[excelRowCount].recipientAddressState
          order.recipientAddressStateCode = this.excelRowList[excelRowCount].recipientAddressStateCode
          order.recipientZipCode = this.excelRowList[excelRowCount].recipientZipCode
          order.recipientAddressCountryCode = this.excelRowList[excelRowCount].recipientAddressCountryCode

          order.shipmentActualWeight = this.excelRowList[excelRowCount].shipmentActualWeight
          order.shipmentVolumeWeight = this.excelRowList[excelRowCount].shipmentVolumeWeight
          order.weightUnit = this.excelRowList[excelRowCount].weightUnit
          order.dimensionWidth = this.excelRowList[excelRowCount].dimensionWidth
          order.dimensionLength = this.excelRowList[excelRowCount].dimensionLength
          order.dimensionHeight = this.excelRowList[excelRowCount].dimensionHeight
          order.packageType = this.excelRowList[excelRowCount].packageType
