                                <tr style="height:50px" :key="index">
                                    <td align="center">
                                        <v-text-field hide-details label="Name (*)" placeholder="Name (*)" outlined v-model="item.itemName" ref="itemName" dense style="width: 100px" ></v-text-field>
                                    </td>
                                    <td align="center">
                                        <v-text-field hide-details label="Quantity (*)" placeholder="Quantity (*)" outlined v-model="item.itemQuantity" ref="itemQuantity" dense style="width: 100px"></v-text-field>
