<script>
import common from "@/common";
import $ from "jquery";
import commFunction from "@/common/Function";
import axios from "axios";

export default {
    props: {
        setOrderRegistration: { 
          type: Object 
        }
    },
    data() {
        return {
            senderDataSet: false,
            orderDateMenu: false,
            senderAddressStateItems : [],
            recipientAddressStateItems : [],
            orderDetail: [],
            itemIndex: 0,
            currencyItems: common.currencyItems,
            shippingTermItems: common.shippingTermItems,
            serviceTypeItems: common.serviceTypeItems,
            packageTypeItems: common.packageTypeItems,
            contentsTypeItems: common.contentsTypeItems,
            sellerDetail: "",
        }
    },
    updated() {
    },
    computed: {
        orderRegistrationDialogShow () {
            return this.setOrderRegistration.orderRegistrationDialogShow
        },
    },
    methods: {
        setSenderValue() {
          this.orderDetail.senderName = this.sellerDetail.firstName + " " +  this.sellerDetail.lastName;
          this.orderDetail.senderCompany = this.sellerDetail.companyName;
          this.orderDetail.senderEmail = this.sellerDetail.email;
          this.orderDetail.senderPhoneNo = this.sellerDetail.companyTelNo;
          this.orderDetail.senderMobileNo = this.sellerDetail.phoneNo;
          this.orderDetail.senderAddressCountryCode = this.sellerDetail.addressCountry;
          this.orderDetail.senderAddress1 = this.sellerDetail.address1;
          this.orderDetail.senderAddress2 = this.sellerDetail.address2;
          this.orderDetail.senderAddressCity = this.sellerDetail.addressCity;
          this.orderDetail.senderZipCode = this.sellerDetail.zipCode;
          this.orderDetail.senderAddressState = this.sellerDetail.addressState;
        },
        clearSenderValue() {
          this.orderDetail.senderName = '';
          this.orderDetail.senderCompany = '';
          this.orderDetail.senderEmail = '';
          this.orderDetail.senderPhoneNo = '';
          this.orderDetail.senderMobileNo = '';
          this.orderDetail.senderAddress1 = '';
          this.orderDetail.senderAddress2 = '';
          this.orderDetail.senderAddressCity = '';
