          this.senderDataSet = false;
        },
        onCountrySelectSender({ iso2 }) {
            if(this.orderDetail.senderAddressStateCode != iso2) {
                this.orderDetail.senderAddressStateCode = "";
            }      
            this.orderDetail.senderAddressCountryCode = iso2;
            this.orderDetail.senderAddressState = "";

            $("#senderAddrssStateSelect").hide();
            $("#senderAddrssStateText").hide();

            if(iso2 == "CA") {
                this.senderAddressStateItems = common.stateCAItems;
                $("#senderAddrssStateSelect").show();
            } else if (iso2 == "IN") { 
                this.senderAddressStateItems = common.stateINItems;
                $("#senderAddrssStateSelect").show();
            } else if (iso2 == "MX") { 
                this.senderAddressStateItems = common.stateMXItems;
                $("#senderAddrssStateSelect").show();
            } else if (iso2 == "US") { 
                this.senderAddressStateItems = common.stateUSItems;
                $("#senderAddrssStateSelect").show();
            } else if (iso2 == "AE") { 
                this.senderAddressStateItems = common.stateAEItems;
                $("#senderAddrssStateSelect").show();
            } else {
                $("#senderAddrssStateText").show();
            }
        },
        onCountrySelectRecipient({ iso2 }) {
            if(this.orderDetail.recipientAddressCountryCode != iso2) {
                this.orderDetail.recipientAddressStateCode = "";
            }
            this.orderDetail.recipientAddressCountryCode = iso2;
            this.orderDetail.recipientAddressState = "";

            $("#recipientAddrssStateSelect").hide();
            $("#recipientAddrssStateText").hide();

            if(iso2 == "CA") {
                this.recipientAddressStateItems = common.stateCAItems;
                $("#recipientAddrssStateSelect").show();
            } else if (iso2 == "IN") { 
                this.recipientAddressStateItems = common.stateINItems;
                $("#recipientAddrssStateSelect").show();
            } else if (iso2 == "MX") { 
                this.recipientAddressStateItems = common.stateMXItems;
                $("#recipientAddrssStateSelect").show();
            } else if (iso2 == "US") { 
                this.recipientAddressStateItems = common.stateUSItems;
                $("#recipientAddrssStateSelect").show();
            } else if (iso2 == "AE") { 
                this.recipientAddressStateItems = common.stateAEItems;
                $("#recipientAddrssStateSelect").show();
            } else {
                $("#recipientAddrssStateText").show();
            }
        },
        onCountrySelectItemOrigin({ iso2 }) {
            this.orderDetail.orderItems[this.itemIndex].itemOriginCountryCode = iso2;
        },
        setItemIndex(index) {
            this.itemIndex = index;
        },
        onSenderAddressStateCodeChange() {
            for(var senderStateCount = 0; senderStateCount < this.senderAddressStateItems.length; senderStateCount++) {
                if(this.orderDetail.senderAddressStateCode == this.senderAddressStateItems[senderStateCount].value) {
                    this.orderDetail.senderAddressState = this.senderAddressStateItems[senderStateCount].name;
                }
            }
        },
        onRecipientAddressStateCodeChange() {
            for(var recipientStateCount = 0; recipientStateCount < this.recipientAddressStateItems.length; recipientStateCount++) {
                if(this.orderDetail.recipientAddressStateCode == this.recipientAddressStateItems[recipientStateCount].value) {
                    this.orderDetail.recipientAddressState = this.recipientAddressStateItems[recipientStateCount].name;
                }
            }
        },
        addItem() {
            var orderItem = new Object();
            orderItem.itemName = '';
            orderItem.itemQuantity = '';
            orderItem.itemUnitValue = '';
            orderItem.itemValueCurrency = '';
            orderItem.itemWeight = '';
            orderItem.itemHSCode = '';
            orderItem.itemOriginCountryCode = 'KR';
            orderItem.itemUrl = '';
            orderItem.itemBarcode = '';
            orderItem.itemSKU = '';
            orderItem.parcelNo = '';
            this.itemIndex = this.orderDetail.orderItems.length;
            this.orderDetail.orderItems.push(orderItem)
        },
        removeItem(index) {
            if(this.orderDetail.orderItems.length == 1) {
                this.$alert(common.messages.orderItemCheck, "", "info").then(() => { });
                return false;
            } else {
                this.orderDetail.orderItems.splice(index, 1)
            }
        },      
        init() {
            this.orderDetail = [];
            this.orderItems = [];

            var orderDetailInit = new Object();
            orderDetailInit.storePlatform = '';
            orderDetailInit.storeName = '';
